/* Header.css */

.navbar.bg-custom {
    background-color: #61dafb; /* Change this color as per your requirement */
  }
  
  .navbar-brand {
    color: #000;
  }
  
  .navbar-date {
    margin-left: auto; /* Push the date to the right side */
    padding: 5px;
    color: #000;
    font-size: 1.2em;
    text-align: right; /* Align the text to the right */
  }
  
  .navbar-date div {
    margin: 0;
    line-height: 1.2em;
  }
  
  @media (max-width: 576px) {
    .navbar-date {
      font-size: 1em;
    }
  }
  