.todos-container {
    background: rgba(0, 0, 0, 0.5); /* Dark transparent background */
    border-radius: 15px;
    padding: 20px;
    max-width: 600px;
    margin: 20px auto;
    transition: height 0.3s ease; /* Smooth height transition */
  }
  
  .todo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background: #fff;
    border-radius: 10px;
    margin-bottom: 10px;
    word-wrap: break-word;
  }
  
  .todo-check {
    flex: 0 0 28px; /* Fixed width */
  height: 28px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  margin-right: 10px;
  cursor: pointer;
  background-image: url('../images/unchecked.png');
  }
  
  .todo-check.checked {
    background-image: url('../images/chacked2.jpg');
  }

  .todo-title {
    flex: 1; /* Allow the title to grow and fill the remaining space */
    overflow-wrap: anywhere; /* Ensure long text wraps within the container */
  }

  .todo-item .completed {
    text-decoration: line-through;
    color: #888;
  }
  
  .delete-btn {
    cursor: pointer;
    color: red;
    font-size: 20px;
  }
  