/* App.css */


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  font-family: "Roboto Slab", serif;
  font-optical-sizing: auto;
  font-weight: 800;
  font-style: normal;
}

.main-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 20px;
}

.sidebar {
  width: 20%;
  margin-right: 20px;
  margin-left: -150px;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar li {
  margin: 10px 0;
}

.content {
  width: 50%;
  padding: 20px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  transition: height 0.3s ease;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5); /* Add depth to the container */
}

.container {
  width: 100%;
}

@media (max-width: 576px) {
  .main-container {
    flex-direction: column;
    align-items: center;
  }

  .sidebar {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
    display: flex;
    justify-content: space-around;
    padding: 10px 0;
  }

  .sidebar ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    text-align: center;
  }

  .sidebar li {
    margin: 0 10px;
    text-align: center;
    justify-content: center;
  }

  .content {
    width: 100%;
    margin-top: 20px;
    padding: 20px;
    background: rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    transition: height 0.3s ease;
