}




/* Header.css */

.navbar.bg-custom {
    background-color: #61dafb; /* Change this color as per your requirement */
  }
  
  .navbar-brand {
    color: #000;
  }
  
  .navbar-date {
    margin-left: auto; /* Push the date to the right side */
    padding: 5px;
    color: #000;
    font-size: 1.2em;
    text-align: right; /* Align the text to the right */
  }
  
  .navbar-date div {
    margin: 0;
    line-height: 1.2em;
  }
  
  @media (max-width: 576px) {
    .navbar-date {
      font-size: 1em;
    }
  }
  
/* Sidebar.css */

.sidebar ul {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar li {
    margin: 10px 0;
  }
  
  .sidebar a {
    color: #000000;
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: 10px;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .sidebar a i {
    margin-right: 10px;
    font-size: 1.2em;
  }
  
  .sidebar a:hover {
    background-color: #575757;
    color: #61dafb;
  }
  
  .sidebar a.active {
    background-color: #444;
    color: #61dafb;
  }
  
  .sidebar a.active i {
    color: #61dafb;
  }
  
/* AddTodo.css */
.container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: #edeef0;
  border-radius: 30px;
  padding: 10px;
  margin-bottom: 25px;
  width: 100%;
}

.form-container input {
  flex: 1 1;
  border: none;
  outline: none;
  background: transparent;
  padding: 10px;
  margin-left: 10px; /* Add some space between the button and the input */
  /* border-top-left-radius: 30px;
  border-bottom-left-radius: 30px; */
}

.form-container button {
  border: none;
  outline: none;
  padding: 12px 20px; /* Adjust padding for the button */
  background: #007500;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  border-radius: 20px;
  /* border-top-right-radius: 30px;
  border-bottom-right-radius: 30px; */
}


/* Adjust the font size for the input field's placeholder text for different screen sizes */
@media (max-width: 768px) {
  .form-container input::placeholder {
    font-size: 0.9em;
  }
}

@media (max-width: 480px) {
  .form-container input::placeholder {
    font-size: 0.8em;
  }
}

.form-container button {
  border: none;
  outline: none;
  padding: 12px 20px; /* Adjust padding for the button */
  background: #007500;
  color: #fff;
  font-size: 15px;
  cursor: pointer;
  border-radius: 20px;
}
.todos-container {
    background: rgba(0, 0, 0, 0.5); /* Dark transparent background */
    border-radius: 15px;
    padding: 20px;
    max-width: 600px;
    margin: 20px auto;
    transition: height 0.3s ease; /* Smooth height transition */
  }
  
  .todo-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    background: #fff;
    border-radius: 10px;
    margin-bottom: 10px;
    word-wrap: break-word;
  }
  
  .todo-check {
    flex: 0 0 28px; /* Fixed width */
  height: 28px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  margin-right: 10px;
  cursor: pointer;
  background-image: url(/TodoList_React/static/media/unchecked.9c05c929.png);
  }
  
  .todo-check.checked {
    background-image: url(/TodoList_React/static/media/chacked2.6bc7124a.jpg);
  }

  .todo-title {
    flex: 1 1; /* Allow the title to grow and fill the remaining space */
    overflow-wrap: anywhere; /* Ensure long text wraps within the container */
  }

  .todo-item .completed {
    text-decoration: line-through;
    color: #888;
  }
  
  .delete-btn {
    cursor: pointer;
    color: red;
    font-size: 20px;
  }
  
