/* AddTodo.css */
.container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  background: #edeef0;
  border-radius: 30px;
  padding: 10px;
  margin-bottom: 25px;
  width: 100%;
}

.form-container input {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  padding: 10px;
  margin-left: 10px; /* Add some space between the button and the input */
  /* border-top-left-radius: 30px;
  border-bottom-left-radius: 30px; */
}

.form-container button {
  border: none;
  outline: none;
  padding: 12px 20px; /* Adjust padding for the button */
  background: #007500;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  border-radius: 20px;
  /* border-top-right-radius: 30px;
  border-bottom-right-radius: 30px; */
}


/* Adjust the font size for the input field's placeholder text for different screen sizes */
@media (max-width: 768px) {
  .form-container input::placeholder {
    font-size: 0.9em;
  }
}

@media (max-width: 480px) {
  .form-container input::placeholder {
    font-size: 0.8em;
  }
}

.form-container button {
  border: none;
  outline: none;
  padding: 12px 20px; /* Adjust padding for the button */
  background: #007500;
  color: #fff;
  font-size: 15px;
  cursor: pointer;
  border-radius: 20px;
}