/* Sidebar.css */

.sidebar ul {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar li {
    margin: 10px 0;
  }
  
  .sidebar a {
    color: #000000;
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: 10px;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .sidebar a i {
    margin-right: 10px;
    font-size: 1.2em;
  }
  
  .sidebar a:hover {
    background-color: #575757;
    color: #61dafb;
  }
  
  .sidebar a.active {
    background-color: #444;
    color: #61dafb;
  }
  
  .sidebar a.active i {
    color: #61dafb;
  }
  